.card {
  border: #ffffff solid 4px;
  height: 20%;
  margin: 2%;
  position: relative;
  width: 20%;
  display: inline-block;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#000000;
}
.card > .img-container > img {
  width: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}
